@font-face {
	font-family: Digital;
	src: url(/static/media/digital-subset.55ebeeb7.ttf);
}

@font-face {
	font-family: 'Lilita One';
	src: url(/static/media/LilitaOne-Regular.0d4e1383.ttf);
}

* {
	box-sizing: border-box;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

body {
	background-color: #34495e;
}

.container {
	height: 75vh;
	width: 75vh;
	background-color: #2c3e50;
	position: relative;
	margin: 10vh auto;
	padding: 2vh;
	overflow: hidden;
	border-radius: 50%;
}

.button {
	height: 33vh;
	width: 33vh;
	box-sizing: border-box;
	margin: 1vh;
	display: inline-block;
}

.button:hover {
	background-color: rgba(255, 0, 0, 50%);
}

.button:active {
	background-color: rgba(255, 0, 0, 80%);
}

.button:nth-of-type(1) {
	border-radius: 500px 0 0 0;
	background-color: #e74c3c
}
.button:nth-of-type(1):active {
	background-color: #c0392b
}

.button:nth-of-type(2) {
	border-radius: 0 500px 0 0;
	background-color: #f1c40f;
}
.button:nth-of-type(2):active {
	background-color: #f39c12;
}

.button:nth-of-type(3) {
	border-radius: 0 0 0 500px;
	background-color: #2ecc71;
}
.button:nth-of-type(3) {
	background-color: #27ae60;
}

.button:nth-of-type(4) {
	border-radius: 0 0 500px 0;
	background-color: #9b59b6;
}
.button:nth-of-type(4):active {
	border-radius: 0 0 500px 0;
	background-color: #8e44ad;
}

.controls {
	height: 50%;
	width: 50%;
	border: 2vh solid #2c3e50;
	position: absolute;
	border-radius: 50%;
	top: 50%;
	left: 50%;
	margin: -25% 0 0 -25%;
	background-color: white;
	z-index: 1;
	overflow: hidden;
}

.controls * {
	position: relative;
	display: inline-block;
}

.control-buttons {
	height: 100%;
	width: 100%;
	position: relative;
}

.game-light {
	height: 20%;
	width: 10%;
	background-color: #27ae60;
	border: .33vh solid #2c3e50;
	border-radius: 50%;
	position: absolute;
	left: 65%;
	top: 55%;
}

.game-controls {
	height: 50%;
	width: 100%;
	border-top: 1vh solid #2c3e50;
	text-align: center;
	border-radius: 0 0 500px 500px;
	background-color: #ecf0f1
}

.game-reset {
	height: 20%;
	width: 10%;
	background-color: #c0392b;
	border-radius: 50%;
	border: .33vh solid #2c3e50;
	position: absolute;
	left: 25%;
	top: 55%;
}

.game-name {
	height: 50%;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #bdc3c7;
}

.game-name span {
	font-size: 7vh;
	margin-top: 2vh;
	color: #2c3e50;
	font-family: 'Lilita One', monospace;
	text-transform: uppercase;
}

.game-switch {
	height: 20%;
	width: 20%;
	background-color: #2c3e50;
	position: absolute;
	left: 40%;
	top: 60%;
	border-radius: 5px;
}

.game-slider {
	height: 80%;
	width: 40%;
	background-color: #7f8c8d;
	position: absolute;
	top: 10%;
	left: 5%;
	border-radius: 2px;
}

.game-score {
	height: 40%;
	width: 30%;
	border-radius: 5px;
	position: absolute;
	top: 1vh;
	left: 35%;
	background-color: #7f8c8d;
	display: flex;
	align-items: center;
	justify-content: center;
}

.game-score span {
	height: 90%;
	width: 91.5%;
	color: #7f8c8d;
	font-family: Digital;
	background-color: #2c3e50;
	padding: .5vh;
	text-align: center;
	vertical-align: middle;
	font-size: 6vh;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 3px;
}

.button-toggleScheme {
	height: 1em;
	width:1em;
	float: left;
	position: absolute;
	top: .5vh;
	left: .5vh;
	background:transparent;
	border: .1vh solid #2c3e50;
	border-radius: 50%;
}
.button-toggleScheme:hover {
	background-color: #2c3e50;
}

.button-toggleScheme:focus {
	outline: none;
  }
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

